@tailwind base;
@tailwind components;
html, body {
  @apply m-0;
  @apply p-0;
}

body {
  @apply bg-white;
  @apply text-sm;
}

.text-list,
.text-list-header {
  @apply my-2;
}

input[type=tel],
input[type=text] {
  @apply m-0;
  @apply px-3;
  @apply py-1;
  @apply appearance-none;
  @apply bg-gray-200;
  @apply border-2;
  @apply border-gray-200;
  @apply rounded;
  @apply text-gray-700;
}
@screen md {
  input[type=tel],
input[type=text] {
    @apply mx-2;
  }
}
input[type=tel]:focus,
input[type=text]:focus {
  @apply outline-none;
  @apply bg-white;
  @apply border-orange-500;
}

.btn {
  @apply bg-orange-500;
  @apply text-white;
  @apply px-3;
  @apply py-1;
  @apply rounded;
  @apply border-2;
  @apply border-orange-500;
}

@tailwind utilities;